import logo from './logo.svg';
import './App.css';
import { Grid } from '@mantine/core';
import { Autocomplete } from '@mantine/core';
import { NumberInput } from '@mantine/core';
import { useState } from 'react';


import { useForm } from '@mantine/form';
import { TextInput, Switch, Group, ActionIcon, Box, Text, Button, Code } from '@mantine/core';
import { randomId } from '@mantine/hooks';
import { Trash } from 'tabler-icons-react';

const stock_tickers = ['TSLA', 'COST', 'WFC', 'AMD', 'IBM', 'C',
                       'JPM', 'HD', 'NKE', 'CAT', 'MRK', 'WMT',
                       'FB', 'BABA', 'NVDA', 'PFE', 'CMCSA',
                       'T', 'CVX', 'INTC', 'XOM', 'INTU',
                       'AMZN', 'GM', 'GOOGL', 'AAPL', 'BA', 'KO', 'CSCO',
                       'BAC', 'GE', 'MSFT', 'UNH', 'VZ', 'ORCL', 'V', 'CRM',
                      'JNJ', 'ABBV', 'PEP', 'QCOM', 'BP', 'DIS', 'GS', 'NFLX', 'PYPL', 'MCD'];

                      
                      
function Demo() {

  const [graph, setGraph] = useState(<div>Hello world</div>);

  const sendRequest =  async () => {

    var data = {}

    for (var stock in form.values.stocks){
      data[form.values.stocks[stock].stock_ticker] = form.values.stocks[stock].percentage;
    }

    try {
      console.log(data);
      const response = await fetch('https://pgdata.onrender.com/plot', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          // You can add additional headers here if needed
        },
        body: JSON.stringify(data),
      });

      if (!response.ok) {
        throw new Error(`HTTP error! Status: ${response.status}`);
      }

      const responseData = await response.json();
      console.log('Response data:', responseData);
    } catch (error) {
      console.error('Error:', error.message);
    }

  } 


  const form = useForm({
    initialValues: {
      stocks: [{ stock_ticker: '', percentage: 0, key: randomId() }],
    },
  });

  const fields = form.values.stocks.map((item, index) => (
    <Group key={item.key} mt="xs">
      <Autocomplete
        placeholder="Pick value or enter anything"
        data={stock_tickers}
        style={{ flex: 1 }}
        {...form.getInputProps(`stocks.${index}.stock_ticker`)}
      />
      <NumberInput
      placeholder="Percentage of stock in initial portfolio"
      style={{ flex: 1 }}
      {...form.getInputProps(`stocks.${index}.percentage`)}
      >

      </NumberInput>
      <ActionIcon color="red" onClick={() => form.removeListItem('stocks', index)}>
        <Trash size="1rem" />
      </ActionIcon>
    </Group>
  ));

  return (
    <Box maw={1500} mx="auto">
      {fields.length > 0 ? (
        <Group mb="xs">
          <Text fw={500} size="sm" style={{ flex: 1 }}>
            Stock ticker
          </Text>
          <Text fw={500} size="sm" pr={90}>
            Percentage
          </Text>
        </Group>
      ) : (
        <Text c="dimmed" ta="center">
          No stocks...
        </Text>
      )}

      {fields}

      <Group justify="center" mt="md">
        <Button
          onClick={() =>
            form.insertListItem('stocks', { stock_ticker: '', percentage: 0, key: randomId() })
          }
        >
          Add stock to portfolio list
        </Button>
      </Group>
          <Button onClick={sendRequest}>Send request</Button>

        {graph}

    </Box>
  )
}
                      

                      
                      
                      

function App() {


  return (
    <div className="App">



      <Demo></Demo>
      <h1>Stock sim</h1>

  

    
    </div>
  );
}

export default App;
